    import switchc from '@/components/zz-switchc/zz-switchc.vue'
	export default{
		data(){
			return{
				phone: '',
				password: '',
				is_success: false
			}
		},
		components: {
			switchc
		},
		methods:{
			switchchange(e) {
				console.log(e);
			},
			clearPhone(){
				this.phone = '';
			},
			clearPwd(){
				this.password = '';
			},
			toCreatePage(){
				uni.navigateTo({
					url: '/pages/login/register'
				})
			},
			toForgetPwd(){
				uni.navigateTo({
					url: '/pages/login/forgetPassword'
				})
			},
			loginSubmit(){
				if(this.phone == ''){
					uni.showToast({
						title: '请输入登录手机号',
						icon: 'none'
					})
					return;
				}
				if(!(/^1[3456789]\d{9}$/.test(this.phone))){
					uni.showToast({
						title: '手机号码格式不正确',
						icon: 'none'
					})
					return; 
				}
				if(this.password == ''){
					uni.showToast({
						title: '请输入登录密码',
						icon: 'none'
					})
					return;
				}
				// if(){
				// 	uni.showToast({
				// 		title: '账号或密码不正确',
				// 		icon: 'none'
				// 	})
				// 	return;
				// }
				this.is_success = true;
				uni.showToast({
					title: '登录成功',
					icon: 'success',
					duration: 2000
				})
				setTimeout(function(){
					uni.reLaunch({
						url: '/pages/member/index'
					})
				},2000)
				
			}
		}
	}
